.die {
  width: 50%;
  display: inline-block;
  margin-bottom: 50px;
}

.die button {
  font-size: 18px;
  display: block;
  margin: auto;
  width: 50px;
  padding: 3px 0;
  border-radius: 5px;
}

.die .result {
  font-size: 40px;
  text-align: center;
  margin-bottom: 10px;
}

@media (min-width: 520px) {
  .die {
    width: 33%;
    margin-bottom: 60px;
  }
}

@media (min-width: 850px) {
  .die {
    margin-bottom: 80px;
  }
}




