#rng {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column-reverse;
      flex-direction: column-reverse;
  -ms-flex-align: center;
      align-items: center;
  padding: 35px 0;
  border-top: 1px solid #b1b1b1;
  border-bottom: 1px solid #b1b1b1;
}

.inputs {
  margin-right: 10px;
}

#rng button {
  width: 100%;
  font-size: 16px;
  padding: 3px 0;
}

#rng .result {
  width: 100%;
  font-size: 40px;
  text-align: center;
  margin-bottom: 10px;
}

.result:after {
  content: '.';
  visibility: hidden;
}

@media (min-width: 520px) {
  #rng {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 500px;
    margin: auto;
  }

  #rng .result {
    font-size: 60px;
  }
}

/* fade animation for result */

@-webkit-keyframes fade {
  from {
    color: #ce4b19;
  }
  to {
    color: black;
  }
}

@keyframes fade {
  from {
    color: #ce4b19;
  }
  to {
    color: black;
  }
}

.result {
  color: #ce4b19;
}

.result.start {
  color: black;
}

.rolled {
  -webkit-animation-name: fade;
          animation-name: fade;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  color: #ce4b19;
}


